.cart-icon{
    color:burlywood;
    font-size: 40px;
    margin-top: 6px;
    margin-left: 100px;
}
.list-container{
    display: flex;
    justify-content: center;
    list-style-type: none;
    gap: 150px;
    background-color: black;
    line-height: 50px;
    border-radius: 30px;
    border: white dashed 2px;
}
.nav-link{
    
    text-decoration: none;
    color: white;
    font-family:'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-weight: bold;
}
.active{
    color: red;
}
a:hover{
    border-bottom: solid 4px yellow;
}
th{
    text-align: center;
    font-size: 30px;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: bold;
    color: firebrick;
}
.table-cart{
    position: relative;
    background-color:ghostwhite;
    width: 85%;
    margin-left: 100px;
    margin-top: 50px;
}