body{
    background-image: url('../assets/Images/netsute.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    height:3000px;
}
.heading{
    color:red;
    letter-spacing: 2px;
}

.ecom-head{
    position: absolute;
    top: 200px;
    left:80px;
}
.ecom-img{
    position: absolute;
    left: 500px;
    top: -100px;
}
.enq-heading{
    font-weight: bold;
    font-family:'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    font-size: 40px;
    color:darkblue;
    
}
.enq-heading::first-letter{
    font-size: 150px;
    color:mediumvioletred;
}
.form{
    position: absolute;
    top:650px;
    background-color:lightblue;
    left:180px;
    width:80%;
    text-align: center;
    line-height: 70px;
}
input{
    width: 600px;
    height: 35px;
    border-radius: 5px;
}
textarea{
    width:600px;
    height:120px;
    border-radius: 5px
}
::placeholder{
    color: red;
}
.btn-submit{
    background-color:limegreen;
    width: 10%;
    height: 40px;
    border-radius: 30px;
    border: none;
    color: black;
    font-weight: bold;
    font-size: 15px;
}