.container{
    display: grid;
    grid-template-columns: repeat(4,250px);
    gap: 180px;
    position: absolute;
    top:100px;
}
.title-shirt{
    position: relative;
    top: -60px;
    color: crimson;
    
    
}
.rate-title{
    margin-top: -60px;
    color: darkmagenta;
}
.men-container{
    margin-left: 30px;
}
.button-cart{
    width:100%;
    margin-top: 5px;
    margin-bottom: 20px;
    height: 40px;
    background-color: limegreen;
    font-weight: bold;
    margin-left: 20px;
    border-radius: 10px;
    border: none;
    cursor: pointer;
    
}
